const { getStorage } = require('../utils/storage.js')
/**
 * httpuni.request()
 * @param {Object} obj - 
 * @param {string} [obj.method] - 
 * @param {string} [obj.url] - 
 * @param {(string|Object|Array|ArrayBuffer)} [obj.data] - 
 * @param {string} [obj.dataType] - 
 * @param {string} [obj.responseType] - 
 * @param {Object} [obj.header] - Header,headerReferer
 * @return {Promise} Promise
 */
function request({
  method = 'GET',
  url = '',
  data = {},
  dataType = 'json',
  responseType = 'text',
  header = {},
  needToken = true,
  showLoading = true,
  loadingText = '',
  hideLoading = true,
  showSucessToast = false,
  successText = "",
  showErrorModal =  true,
  errorText = '',
  returnHeader = false,
  returnErrorCode = false,
  checkCode = true
} = {}) {
  let requestTask = null
  const p = new Promise((resolve,reject) => {
    //Loading
    if(showLoading){
      uni.showLoading({ title: loadingText })
    }
    //token
    if(needToken) {
      const token = getStorage('tempToken')
      url = url +  (url.indexOf('?') === -1 ? '?token=' : '&token=') + token
    }
     requestTask = uni.request({
       method,
       url,
       data,
       dataType,
       responseType,
       header,
       success(res) {
         //loading
         if (hideLoading) {
           uni.hideLoading()
         }
		 if (!checkCode) {
			 return resolve(res);
		 }
         const { statusCode, header, msg } = res
         if(statusCode === 200){
           const { data } = res
           const { code } = data
           if (code === 0) {
             //Toast
             if(showSucessToast){
               uni.showToast({
                 title: successText,
               })
             }
             if(returnHeader){
               resolve({ header,data:data.data })
             } else {
               resolve(data.data)
             }
           } else {
             const msg = `${errorText}: ${statusCode} - ${data.message || data.msg}`
             //Modal
             if(showErrorModal){
               uni.showModal({
                 content: msg,
                 showCancel:false
               })
             }
             if(returnErrorCode) {
               reject({ code, msg })
             } else {
               reject(msg)
             }
           }
         }else{
           const msg = `${errorText}: ${statusCode}-${res.errMsg}`
           // Modal
           if (showErrorModal) uni.showModal({ content: msg, showCancel: false })
           reject(msg)
         }
       },
       fail(res) {
         if(hideLoading){
           uni.hideLoading()
         }
         //Modal
         if(showErrorModal){
           uni.showModal({
             content: '',
             showCancel: false
           })
         }
         reject(res)
       }

     }) 
  })
  //requestTask
  p.abort = function() {
    requestTask.abort()
  }
  return p
}

module.exports = {
  request,
}