/**
 * uni.setStorage()uni.setStorageSync()
 * @param {string} key - key
 * @param {string|Object} data - 
 * @param {boolean} isSync - 
 * @return {void|Promise} 
 */
function setStorage(key = '', data = {}, isSync = true) {
  if(isSync) {
    return uni.setStorageSync(key, data)
  } else {
    return new Promise((resolve, reject) => {
      uni.setStorage({
        key,
        data,
        success: function() {
          resolve();
        },
        fail(res) {
          reject();
        }
      })
    })
  }
}

/**
 * uni.getStorage()uni.getStorageSync()
 * @param {string} key - key
 * @param {boolean} isSync -  
 * @return {string|Promise}
 */
function getStorage(key = '', isSync = true) {
  if (isSync) {
    return uni.getStorageSync(key)
  } else {
    return new Promise((resolve, reject) => {
      uni.getStorage({
        key,
        success(res) { resolve(res.data) },
        fail(res) { reject(res) }
      })
    })
  }
}


/**
 * uni.removeStorage()uni.removeStorageSync()
 * @param {string} key - key
 * @param {boolean} isSync - 
 * @return {void|Promise} 
 */
function removeStorage(key = '', isSync = true) {
  if (isSync) {
    return uni.removeStorageSync(key)
  } else {
    return new Promise((resolve, reject) => {
      uni.removeStorage({
        key,
        success() { resolve() },
        fail(res) { reject() }
      })
    })
  }
}

/**
 * uni.clearStorage()uni.clearStorageSync()
 * @param {boolean} isSync - 
 * @return {void|Promise} 
 */
function clearStorage(isSync = true) {
  if (isSync) {
    uni.clearStorageSync()
  } else {
    return new Promise((resolve, reject) => {
      uni.clearStorage({
        success() { resolve() },
        fail(res) { reject(res) }
      })
    })
  }
}

/**
 * uni.getStorageInfo()uni.getStorageInfoSync()
 * @param {boolean} isSync - 
 * @return {Object|Promise} 
 */
function getStorageInfo(isSync = true) {
  if (isSync) {
    return uni.getStorageInfoSync()
  } else {
    return new Promise((resolve, reject) => {
      uni.getStorageInfo({
        success(res) { resolve(res) },
        fail(res) { reject(res) }
      })
    })
  }
}

module.exports = {
  setStorage,
  getStorage,
  removeStorage,
  clearStorage,
  getStorageInfo
}