const duration = 2000
import {
	uploadImageUrl
} from './common.js'
/**
 * uni-appapi
 */

/**
 * uni.uni.makePhoneCall
 * @param {string} phone - 
 */
function makePhoneCall({
	phone = ''
} = {}) {
	uni.makePhoneCall({
		phoneNumber: phone,
		success() {

		},
		fail() {
			uni.showToast({
				icon: 'none',
				title: '',
				duration,
			})
		}
	})
}

/**
 * uni.downloadFile
 * @param {string[]} urls - 
 * @param {Object} header - HeaderheaderReferer
 * headerContent-Type
 * @return {Promise} Promise
 */
function downloadFile({
	urls = [],
	header = {}
} = {}) {
	/**
	 * 
	 * @param {string} url - url
	 * @return {Promise} Promise 
	 */
	function createPromise(url) {
		let downloadTask = null
		const p = new Promise((resolve, reject) => {
			downloadTask = uni.downloadFile({
				url,
				header,
				success(res) {
					const {
						statusCode
					} = res
					if (statusCode === 200) {
						const {
							tempFilePath
						} = res
						resolve(tempFilePath)
					} else {
						const {
							errMsg
						} = res
						const err = `${statusCode}-${errMsg}`
						reject(err)
					}
				},
				fail(res) {
					const err = `${res}`
					reject(err)
				},
			})
		})
		// downloadTask
		p.onProgressUpdate = function(callback) {
			downloadTask.onProgressUpdate((res) => {
				callback(res)
			})
		}
		p.abort = function() {
			downloadTask.abort()
		}
		return p
	}
	// 
	const len = urls.length
	if (len === 1) {
		return new Promise((resolve, reject) => {
			// Loading
			//uni.showLoading({ title: '' })
			createPromise(urls[0])
				.then(tempFilePath => thenHandle(tempFilePath, resolve))
				.catch(err => catchHandle(err, reject))
		})
	}
	if (len > 1) {
		return new Promise((reslove, reject) => {
			// Loading
			//uni.showLoading({ title: '' })
			Promise.all(urls.map(item => createPromise(item)))
				.then(tempFilePaths => thenHandle(tempFilePaths, resolve))
				.catch(err => catchHandle(err, reject))
		})
	}
}

/**
 * uni.chooseImage,uni.uploadFile
 */
//
function singleImage({
	url = uploadImageUrl,
	fn = () => {},
	needAlbum = true,
	needCamera = true
} = {}) {
	let sourceType = [];
	if (needAlbum) {
		sourceType.push('album')
	}
	if (needCamera) {
		sourceType.push('camera')
	}
	uni.chooseImage({
		count: 1,
		sizeType: ['original', 'compressed'],
		sourceType,
		success: function(res) {
			const tempFilePaths = res.tempFilePaths;
			uni.showLoading()
			uni.uploadFile({
				header: {
					"Content-Type": "multipart/form-data"
				},
				url,
				filePath: tempFilePaths[0],
				name: 'file',
				success: function(res) {
					uni.hideLoading()
					fn(res)
				}
			})
		},
	})
}
//
function muchImages({
	url = uploadImageUrl,
	fn = fn = () => {},
	count = 9,
	needAlbum = true,
	needCamera = true
} = {}) {
	let list = [];
	let sourceType = [];
	if (needAlbum) {
		sourceType.push('album')
	}
	if (needCamera) {
		sourceType.push('camera')
	}
	uni.chooseImage({
		count,
		sizeType: ['original', 'compressed'],
		sourceType,
		success: function(res) {
			const tempFilePaths = res.tempFilePaths;
			tempFilePaths.forEach(async (filePath) => {
				await uni.uploadFile({
					url,
					filePath,
					name: 'file',
					header: {
						"Content-Type": "multipart/form-data"
					},
					success: function(res) {
						fn(res)
					},
					fail(res) {
						uni.showModal({
							content: res.msg,
							showCancel: false
						})
					}
				})
			})
		},
	})
}

/**
 *  Model
 */
function model({
	title = '',
	content = '',
	confirmText = '',
	showCancel = true,
	successFunction = () => {},
	failFunction = () => {}
} = {}) {
	return new Promise((resolve, reject) => {
		setTimeout(() => {
			wx.showModal({
				title,
				content,
				confirmText,
				showCancel,
				cancelColor: '#888888',
				confirmColor: '#000',
				success: res => {
					resolve(res);
				},
				fail: err => {
					reject(res);
				}
			})
		}, 100);
	});
}

/**
 *  TOAST
 */
function toast({
	title = '',
	mask = true,
	icon = 'loading',
	duration = 1000
} = {}) {
	return new Promise((resolve, reject) => {
		setTimeout(() => {
			wx.showToast({
				title: title,
				mask: mask,
				icon: icon,
				duration: duration,
				success(res) {
					resolve(res);
				},
				fail(res) {
					reject(res);
				}
			})
		}, 100);
	});
}

/**
 * 
 */
function wxpay(wxJsApiParam) {
	return new Promise((resolve, reject) => {
		uni.requestPayment({
			...wxJsApiParam,
			success(res) {
				toast({
					title: "",
					icon: "success"
				}).then(res => {
					resolve({
						type: 'success',
						msg: res
					})
				})
			},
			fail(res) {
				if (res.errMsg !== 'requestPayment:fail cancel') { //()
					model({
						title: '',
						content: res.errMsg,
						showCancel: false
					}).then(res => {
						reject({
							type: 'error',
							msg: res
						})
					});
				} else { //
					model({
						content: '',
						showCancel: false
					}).then(res => {
						resolve({
							type: 'cancel',
							msg: res
						})
					});
				}
			}
		})
	});
}

module.exports = {
	downloadFile,
	singleImage,
	muchImages,
	makePhoneCall,
	toast,
	model,
	wxpay
}
